


/**
 *      id.
 *       ,    
 *
 * @param elementId ID   DOM
 */
export const scrollToElement = (elementId: string) => {
    const element: HTMLElement|null = document.getElementById(elementId);
    const offset: number = element?element.offsetTop - 60:0; //    

    window.scrollTo({
        top: offset,
        behavior:"smooth"
    })

}